import * as THREE from "three";
import { appStore } from "../../immerReducers/appStore";
import { appThree } from "../renderThreejs";

export const screenToWorld = (clientX, clientY): THREE.Vector2 => {
  if (appThree.el === null) {
    throw new Error("no el in dom");
  }

  const { getState } = appStore;
  const state = getState();
  const sceneScale = state.ui.zoomScale;
  const { width, height, top, left } = appThree.el.getBoundingClientRect();
  const screenX = clientX - left;
  const screenY = clientY - top;
  const x = (screenX - width / 2) * sceneScale + appThree.camera.position.x;
  const y = (-screenY + height / 2) * sceneScale + appThree.camera.position.y;
  return new THREE.Vector2(x, y);
};
