import { merge, times } from "lodash";
import * as THREE from "three";
import {
  BeadChildSuffix,
  getThreeNameFromBeadId,
  getThreeUserDataFromBead,
} from "../editorObjects/Bead";
import {
  EditBBoxChildSuffix,
  getThreeNameFromEditBBoxId,
  getThreeUserDataFromEditBBox,
} from "../editorObjects/EditBBox";
import {
  Bead,
  BeadTypes,
  EditBBox,
  Editor,
  NAME_SEPARATOR,
  ObjectTypes,
  Region,
} from "../editorObjects/Editor";
import {
  getThreeNameFromRegionId,
  getThreeUserDataFromRegion,
  RegionChildSuffix,
} from "../editorObjects/Region";
import { EventNames, getEventEmitter } from "../EventEmitter";
import { getModelBBoxFromBeadType, getModelFromBeadType } from "./loadModels";
import { appThree } from "./renderThreejs";

export const initEventEmitterHandlers = () => {
  const eventEmitter = getEventEmitter();

  eventEmitter.on(EventNames.action_changeSelectedBeads_color, (data) => {
    data.beadIds.forEach((id) => {
      const name = getThreeNameFromBeadId(id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);

      if (object === undefined) {
        return;
      }

      object.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          if (child.material) {
            (child.material as THREE.MeshPhysicalMaterial).color.set(
              data.color
            );
          }
        }
      });
    });
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_height, (data) => {
    data.beads.forEach((bead: Bead) => {
      const name = getThreeNameFromBeadId(bead.id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      const bbox = getModelBBoxFromBeadType(bead.type);
      let width = 1;
      let height = 1;
      let depth = 1;
      if (bbox) {
        width = Math.abs(bbox.max.x - bbox.min.x);
        height = Math.abs(bbox.max.y - bbox.min.y);
        depth = Math.abs(bbox.max.z - bbox.min.z);
      }
      object.scale.x = bead.width / width;
      object.scale.y = bead.height / height;
      object.scale.z = bead.depth / depth;
    });
    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_width, (data) => {
    data.beads.forEach((bead: Bead) => {
      const name = getThreeNameFromBeadId(bead.id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      const bbox = getModelBBoxFromBeadType(bead.type);
      let width = 1;
      let height = 1;
      let depth = 1;
      if (bbox) {
        width = Math.abs(bbox.max.x - bbox.min.x);
        height = Math.abs(bbox.max.y - bbox.min.y);
        depth = Math.abs(bbox.max.z - bbox.min.z);
      }
      object.scale.x = bead.width / width;
      object.scale.y = bead.height / height;
      object.scale.z = bead.depth / depth;
    });
    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_depth, (data) => {
    data.beads.forEach((bead: Bead) => {
      const name = getThreeNameFromBeadId(bead.id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      const bbox = getModelBBoxFromBeadType(bead.type);
      let width = 1;
      let height = 1;
      let depth = 1;
      if (bbox) {
        width = Math.abs(bbox.max.x - bbox.min.x);
        height = Math.abs(bbox.max.y - bbox.min.y);
        depth = Math.abs(bbox.max.z - bbox.min.z);
      }
      object.scale.x = bead.width / width;
      object.scale.y = bead.height / height;
      object.scale.z = bead.depth / depth;
    });
    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_type, (data) => {
    data.beadIds.forEach((id) => {
      const name = getThreeNameFromBeadId(id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      appThree.scene.remove(object);
    });

    data.beads.forEach((bead: Bead) => {
      triggerAddBeadToScene(bead);
      triggerSelectBead(bead.id, true);
    });

    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_rotate, (data) => {
    data.beadIds.forEach((id) => {
      const name = getThreeNameFromBeadId(id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }

      object.rotation.x = THREE.MathUtils.DEG2RAD * data.rotateX;
      object.rotation.y = THREE.MathUtils.DEG2RAD * data.rotateY;
      object.rotation.z = THREE.MathUtils.DEG2RAD * data.rotateZ;
    });

    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_selectBeads, (data) => {
    data.diffStates.forEach((bead) => {
      const id = Object.keys(bead)[0];
      const value = Object.values(bead)[0] as boolean;
      triggerSelectBead(id, value);
    });
  });

  const triggerSelectBead = (id: string, shouldSelect: boolean) => {
    const name = getThreeNameFromBeadId(id);
    const object = appThree.scene.getObjectByUserDataProperty("name", name);

    if (object === undefined) {
      return;
    }

    object.traverse((child) => {
      if (child instanceof THREE.Mesh) {
        if (child.material) {
          if (shouldSelect === true) {
            (child.material as THREE.MeshPhysicalMaterial).emissive.set(
              0x171717
            );
          } else {
            (child.material as THREE.MeshPhysicalMaterial).emissive.set(0);
          }
        }
      }
    });
  };

  eventEmitter.on(EventNames.action_selectIntersectObjects, (data) => {
    if (data?.selectedBeads?.diffStates) {
      data.selectedBeads?.diffStates?.forEach((bead) => {
        const id = Object.keys(bead)[0];
        const value = Object.values(bead)[0] as boolean;
        triggerSelectBead(id, value);
      });
    }
    if (data?.selectedRegions?.diffStates) {
      data.selectedRegions?.diffStates?.forEach((region) => {
        const id = Object.keys(region)[0];
        const value = Object.values(region)[0] as boolean;
        triggerSelectRegion(id, value);
      });
    }
    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_updateZoomSCale, (data) => {
    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_selectRegions, (data) => {
    const selectedIds: string[] = [];
    data.diffStates.forEach((bead) => {
      const id = Object.keys(bead)[0];
      const value = Object.values(bead)[0] as boolean;
      triggerSelectRegion(id, value);
      if (value === true) {
        selectedIds.push(id);
      }
    });
  });

  const triggerUpdateEditBBox = (
    editBBox: EditBBox | undefined,
    prevEditBBox: EditBBox | undefined
  ) => {
    if (editBBox === undefined) {
      if (prevEditBBox !== undefined) {
        const name = getThreeNameFromEditBBoxId(prevEditBBox.id);
        const object = appThree.scene.getObjectByUserDataProperty("name", name);
        if (object !== undefined) {
          appThree.scene.remove(object);
        }

        const nameB = getThreeNameFromEditBBoxId(
          prevEditBBox.id,
          EditBBoxChildSuffix.RESIZE_CONTAINER
        );
        const objectB = appThree.scene.getObjectByUserDataProperty(
          "name",
          nameB
        );
        if (objectB !== undefined) {
          appThree.scene.remove(objectB);
        }
      }
      return;
    }

    const rect10x10FromScreenToWorld =
      appThree.camera.userData["rect10x10FromScreenToWorld"];
    const dotSize = (rect10x10FromScreenToWorld.w ?? 10) * 0.5;

    if (prevEditBBox === undefined) {
      const editBBoxThree = new THREE.Group();

      const geometry = new THREE.PlaneGeometry(1, 1, 1, 1);
      const material = new THREE.MeshBasicMaterial({
        color: "#5794fc",
        side: THREE.FrontSide,
        opacity: 0.1,
        transparent: true,
      });
      const editBBoxBgThree = new THREE.Mesh(geometry, material);
      editBBoxBgThree.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.BG
      );

      const bbox = new THREE.Box3();

      if (editBBox.beadIds.length > 0) {
        editBBox.beadIds.forEach((beadId) => {
          const name = getThreeNameFromBeadId(beadId);
          const object = appThree.scene.getObjectByUserDataProperty(
            "name",
            name
          );
          if (object === undefined) {
            return;
          }
          bbox.expandByObject(object);
        });
      }

      if (editBBox.regionsIds.length > 0) {
        editBBox.regionsIds.forEach((regionsId) => {
          const name = getThreeNameFromRegionId(regionsId);
          const object = appThree.scene.getObjectByUserDataProperty(
            "name",
            name
          );
          if (object === undefined) {
            return;
          }
          bbox.expandByObject(object);
        });
      }

      const x = bbox.min.x;
      const y = bbox.min.y;
      const w = Math.abs(bbox.max.x - bbox.min.x);
      const h = Math.abs(bbox.max.y - bbox.min.y);
      const z = 1;

      editBBoxThree.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.MAIN
      );
      editBBoxThree.position.x = x + w / 2;
      editBBoxThree.position.y = y + h / 2;
      editBBoxThree.position.z = -500;
      editBBoxThree.scale.x = w;
      editBBoxThree.scale.y = h;
      editBBoxThree.scale.z = z;
      editBBoxThree.add(editBBoxBgThree);

      const editBBoxResizeThree = new THREE.Group();
      editBBoxResizeThree.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.RESIZE_CONTAINER
      );
      editBBoxResizeThree.scale.x = 1;
      editBBoxResizeThree.scale.y = 1;
      editBBoxResizeThree.scale.z = 1;
      const __material = new THREE.MeshLambertMaterial({ color: "#ffffff" });
      const __boxGeometry = new THREE.CircleGeometry(1, 16);
      const _whiteDot = new THREE.Mesh(__boxGeometry, __material);
      _whiteDot.scale.x = 0.8;
      _whiteDot.scale.y = 0.8;
      _whiteDot.renderOrder = 2;

      const ___material = new THREE.MeshLambertMaterial({ color: "#a9a9a9" });
      const ___boxGeometry = new THREE.CircleGeometry(1, 16);
      const _grayDot = new THREE.Mesh(___boxGeometry, ___material);
      _grayDot.scale.x = 1;
      _grayDot.scale.y = 1;
      _grayDot.renderOrder = 1;

      const _baseDot = new THREE.Group();
      _baseDot.attach(_grayDot);
      _baseDot.attach(_whiteDot);

      const _dotTL = _baseDot.clone();
      _dotTL.position.x = bbox.min.x;
      _dotTL.position.y = bbox.max.y;
      _dotTL.scale.x = dotSize;
      _dotTL.scale.y = dotSize;
      _dotTL.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
      );
      editBBoxResizeThree.attach(_dotTL);
      const _dotTR = _baseDot.clone();
      _dotTR.position.x = bbox.max.x;
      _dotTR.position.y = bbox.max.y;
      _dotTR.scale.x = dotSize;
      _dotTR.scale.y = dotSize;
      _dotTR.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
      );
      editBBoxResizeThree.attach(_dotTR);
      const _dotBL = _baseDot.clone();
      _dotBL.position.x = bbox.min.x;
      _dotBL.position.y = bbox.min.y;
      _dotBL.scale.x = dotSize;
      _dotBL.scale.y = dotSize;
      _dotBL.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
      );
      editBBoxResizeThree.attach(_dotBL);
      const _dotBR = _baseDot.clone();
      _dotBR.position.x = bbox.max.x;
      _dotBR.position.y = bbox.min.y;
      _dotBR.scale.x = dotSize;
      _dotBR.scale.y = dotSize;
      _dotBL.userData = getThreeUserDataFromEditBBox(
        editBBox,
        EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
      );
      editBBoxResizeThree.attach(_dotBR);

      appThree.scene.add(editBBoxResizeThree);
      appThree.scene.add(editBBoxThree);
    } else {
      const name = getThreeNameFromEditBBoxId(prevEditBBox.id);
      const editBBoxThree = appThree.scene.getObjectByUserDataProperty(
        "name",
        name
      );

      if (editBBoxThree === undefined) {
        return;
      }

      const bbox = new THREE.Box3();

      if (editBBox.beadIds.length > 0) {
        editBBox.beadIds.forEach((beadId) => {
          const name = getThreeNameFromBeadId(beadId);
          const object = appThree.scene.getObjectByUserDataProperty(
            "name",
            name
          );
          if (object === undefined) {
            return;
          }
          bbox.expandByObject(object);
        });
      }

      if (editBBox.regionsIds.length > 0) {
        editBBox.regionsIds.forEach((regionsId) => {
          const name = getThreeNameFromRegionId(regionsId);
          const object = appThree.scene.getObjectByUserDataProperty(
            "name",
            name
          );
          if (object === undefined) {
            return;
          }
          bbox.expandByObject(object);
        });
      }

      const x = bbox.min.x;
      const y = bbox.min.y;
      const w = Math.abs(bbox.max.x - bbox.min.x);
      const h = Math.abs(bbox.max.y - bbox.min.y);

      editBBoxThree.position.x = x + w / 2;
      editBBoxThree.position.y = y + h / 2;
      editBBoxThree.position.z = -500;
      editBBoxThree.scale.x = w;
      editBBoxThree.scale.y = h;
      editBBoxThree.scale.z = 1;

      const nameA = getThreeNameFromEditBBoxId(
        editBBox.id,
        EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
      );
      const _dotTL = appThree.scene.getObjectByUserDataProperty("name", nameA);
      if (_dotTL !== undefined) {
        _dotTL.scale.x = dotSize;
        _dotTL.scale.y = dotSize;
        _dotTL.position.x = bbox.min.x;
        _dotTL.position.y = bbox.max.y;
      }
      const nameB = getThreeNameFromEditBBoxId(
        editBBox.id,
        EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
      );
      const _dotTR = appThree.scene.getObjectByUserDataProperty("name", nameB);
      if (_dotTR !== undefined) {
        _dotTR.position.x = bbox.max.x;
        _dotTR.position.y = bbox.max.y;
        _dotTR.scale.x = dotSize;
        _dotTR.scale.y = dotSize;
      }
      const nameC = getThreeNameFromEditBBoxId(
        editBBox.id,
        EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
      );
      const _dotBL = appThree.scene.getObjectByUserDataProperty("name", nameC);
      if (_dotBL !== undefined) {
        _dotBL.position.x = bbox.min.x;
        _dotBL.position.y = bbox.min.y;
        _dotBL.scale.x = dotSize;
        _dotBL.scale.y = dotSize;
      }
      const nameD = getThreeNameFromEditBBoxId(
        editBBox.id,
        EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
      );
      const _dotBR = appThree.scene.getObjectByUserDataProperty("name", nameD);
      if (_dotBR !== undefined) {
        _dotBR.position.x = bbox.max.x;
        _dotBR.position.y = bbox.min.y;
        _dotBR.scale.x = dotSize;
        _dotBR.scale.y = dotSize;
      }
    }
  };

  const triggerSelectRegion = (id: string, shouldSelect: boolean) => {
    const name = getThreeNameFromRegionId(id);
    const object = appThree.scene.getObjectByUserDataProperty("name", name);

    if (object === undefined) {
      return;
    }

    // @INFO: no display change in region
  };

  eventEmitter.on(EventNames.action_deleteBeads, (data) => {
    triggerDeleteBeadIds(data.beadIds);
  });

  eventEmitter.on(EventNames.action_deleteSelectedBeads, (data) => {
    triggerDeleteBeadIds(data.beadIds);
  });

  const triggerDeleteBeadIds = (beadIds: string[]) => {
    beadIds.forEach((beadId) => {
      while (true) {
        const name = getThreeNameFromBeadId(beadId);
        const object = appThree.scene.getObjectByUserDataProperty("name", name);
        if (object === undefined) {
          break;
        }
        appThree.scene.remove(object);
      }
      while (true) {
        const name = getThreeNameFromBeadId(beadId, BeadChildSuffix.ANY);
        const object = appThree.scene.getObjectByUserDataProperty("name", name);
        if (object === undefined) {
          break;
        }
        appThree.scene.remove(object);
      }
    });
  };

  eventEmitter.on(EventNames.action_addBeadOnClick, (data) => {
    const bead = data.bead as Bead;
    triggerAddBeadToScene(bead);
  });

  eventEmitter.on(EventNames.action_updateLight, (data) => {
    const { name, field, value } = data;
    const object = appThree.scene.getObjectByUserDataProperty("name", name);
    if (object !== undefined) {
      const light = object as THREE.DirectionalLight;
      if (data.field === "color") {
        light.color = data.value;
      }
      if (data.field === "intensity") {
        light.intensity = data.value;
      }
    }
  });

  eventEmitter.on(EventNames.action_addRegion, (data) => {
    const region = data.region as Region;
    triggerRegionToScene(region);
  });

  const triggerRegionToScene = (region: Region) => {
    const geometry = new THREE.PlaneGeometry(1, 1, 1, 1);
    // @TODO: zmiana koloru i opacity
    const material = new THREE.MeshBasicMaterial({
      color: "rgb(0,0,0)",
      side: THREE.FrontSide,
      opacity: 0.1,
      transparent: true,
    });
    const regionBgThree = new THREE.Mesh(geometry, material);
    regionBgThree.userData = getThreeUserDataFromRegion(
      region,
      RegionChildSuffix.BG
    );

    const regionGridThree = new THREE.Group();

    times(region.settings.rows).forEach((row, i) => {
      times(region.settings.columns).forEach((column, j) => {
        const width = 1 / region.settings.columns;
        const height = 1 / region.settings.rows;
        const rectWidth = width - 0.0;
        const rectHeight = height - 0.0;
        const rectShape = new THREE.Shape()
          .moveTo(rectWidth / 2, rectHeight / 2)
          .lineTo((rectWidth / 2) * -1, rectHeight / 2)
          .lineTo((rectWidth / 2) * -1, (rectHeight / 2) * -1)
          .lineTo(rectWidth / 2, (rectHeight / 2) * -1);

        rectShape.autoClose = true;

        const _rectPoints = rectShape.getPoints();
        const _rectGeometryPoints = new THREE.BufferGeometry().setFromPoints(
          _rectPoints
        );
        const _lineMaterial = new THREE.MeshBasicMaterial({ color: 0x0040f0 });
        const _cell = new THREE.Line(_rectGeometryPoints, _lineMaterial);
        _cell.userData = getThreeUserDataFromRegion(
          region,
          RegionChildSuffix.GRID_CELL
        );
        _cell.position.x = -1 * 0.5 + column * width + width / 2;
        _cell.position.y = -1 * 0.5 + row * height + height / 2;

        regionGridThree.add(_cell);
      });
    });

    const regionThree = new THREE.Group();
    regionThree.userData = getThreeUserDataFromRegion(
      region,
      RegionChildSuffix.MAIN
    );

    const regionContentThree = new THREE.Group();
    regionContentThree.userData = getThreeUserDataFromRegion(
      region,
      RegionChildSuffix.CONTENT
    );
    regionContentThree.position.x = region.position.x;
    regionContentThree.position.y = region.position.y;
    regionContentThree.position.z = -500;
    regionContentThree.scale.x = region.width;
    regionContentThree.scale.y = region.height;
    regionContentThree.scale.z = 1;
    regionContentThree.add(regionBgThree);
    regionContentThree.add(regionGridThree);
    regionThree.add(regionContentThree);

    appThree.scene.add(regionThree);

    // @TODO: CHeck lines
    // const material = new THREE.LineBasicMaterial({ color: 0x0000ff });
    // material.linewidth = 10
    // const points: THREE.Vector3[] = [];
    // points.push(new THREE.Vector3(-10, 0, 0));
    // points.push(new THREE.Vector3(0, 10, 0));
    // points.push(new THREE.Vector3(10, 0, 0));
    // const geometry = new THREE.BufferGeometry().setFromPoints(points);
    // const line = new THREE.Line(geometry, material);
    // appThree.scene.add(line)
  };

  const triggerAddBeadToScene = (bead: Bead) => {
    const beadType = bead.type;
    const beadModel = getModelFromBeadType(beadType);

    if (beadModel === undefined) {
      console.warn("beadModel not found for type: ", beadType, bead.id);
      return;
    }

    const object = appThree.scene.getObjectByUserDataProperty("id", bead.id);

    if (object !== undefined) {
      console.warn("bead already in scene", bead.id);
      return;
    }

    beadModel.traverse((child) => {
      child.userData = getThreeUserDataFromBead(bead, BeadChildSuffix.ANY);
      if (child instanceof THREE.Mesh) {
        child.material = new THREE.MeshPhongMaterial({
          shininess: 100,
          color: bead.color,
        });

        // child.material = new THREE.MeshLambertMaterial({
        //   color: bead.color,
        // });

        // child.material = new THREE.MeshStandardMaterial({
        //   color: bead.color,
        //   metalness: 0,
        //   roughness: 0.5,
        // });

        // child.material = new THREE.MeshDepthMaterial();

        // child.material = new THREE.MeshPhysicalMaterial({
        //   flatShading: true,
        //   reflectivity: 1,
        //   color: bead.color,
        // });
      }
      if ((child as any).geometry) {
        (child as any).geometry.computeBoundingBox();
      }
    });

    beadModel.userData = getThreeUserDataFromBead(bead);

    const bbox = getModelBBoxFromBeadType(beadType);
    let width = 1;
    let height = 1;
    let depth = 1;
    if (bbox) {
      width = Math.abs(bbox.max.x - bbox.min.x);
      height = Math.abs(bbox.max.y - bbox.min.y);
      depth = Math.abs(bbox.max.z - bbox.min.z);
    }

    beadModel.scale.x = bead.width / width;
    beadModel.scale.y = bead.height / height;
    beadModel.scale.z = bead.depth / depth;

    beadModel.rotation.x = THREE.MathUtils.DEG2RAD * bead.rotateX;
    beadModel.rotation.y = THREE.MathUtils.DEG2RAD * bead.rotateY;
    beadModel.rotation.z = THREE.MathUtils.DEG2RAD * bead.rotateZ;

    beadModel.position.x = bead.position.x;
    beadModel.position.y = bead.position.y;

    appThree.scene.add(beadModel);
  };

  eventEmitter.on(EventNames.action_movedSelected, (data) => {
    data.beads.forEach((bead: Bead) => {
      const name = getThreeNameFromBeadId(bead.id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      object.position.x = bead.position.x;
      object.position.y = bead.position.y;
    });
    data.regions.forEach((region: Region) => {
      const name = getThreeNameFromRegionId(
        region.id,
        RegionChildSuffix.CONTENT
      );
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      object.position.x = region.position.x;
      object.position.y = region.position.y;
    });
    triggerUpdateEditBBox(data.editBBox, data.prevEditBBox);
  });

  eventEmitter.on(EventNames.action_changeBeadColorToBeadTemplate, (data) => {
    data.beads.forEach((bead: Bead) => {
      const name = getThreeNameFromBeadId(bead.id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      object.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          child.material = new THREE.MeshPhysicalMaterial({
            flatShading: true,
            reflectivity: 1,
            color: data.color,
          });
        }
      });
    });
  });

  eventEmitter.on(EventNames.action_fastAddBeads, (data) => {
    Object.keys(data.beads).forEach((id) => {
      const bead = data.beads[id] as Bead;
      triggerAddBeadToScene(bead);
    });

    data.prevSelectedBeadIds.forEach((beadId) => {
      triggerSelectBead(beadId, false);
    });
  });

  eventEmitter.on(EventNames.action_snapToLineSelectedBeads, (data) => {
    data.beads.forEach((bead: Bead) => {
      const name = getThreeNameFromBeadId(bead.id);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        return;
      }
      object.position.x = bead.position.x;
      object.position.y = bead.position.y;
    });
  });

  eventEmitter.on(EventNames.action_onChangeEditorMode, (data) => {
    data.prevSelectedBeadIds.forEach((beadId) => {
      triggerSelectBead(beadId, false);
    });
  });

  eventEmitter.on(EventNames.action_readSavedCanvas, (data) => {
    Object.keys(data.beads).forEach((id) => {
      const bead = data.beads[id] as Bead;
      triggerAddBeadToScene(bead);
    });
    Object.keys(data.regions).forEach((id) => {
      const region = data.regions[id] as Region;
      triggerRegionToScene(region);
    });
  });

  eventEmitter.on(EventNames.startAppEvent, (data: { state: Editor }) => {
    console.log("startAppEvent: ...", data.state.ui.lights);

    if (data.state.ui.lights && data.state.ui.lights.length > 0) {
      data.state.ui.lights.forEach((l) => {
        if (l.type === "THREE.DirectionalLight") {
          const light = new THREE.DirectionalLight(l.color, l.intensity);
          light.position.set(
            l.position?.x ?? 0,
            l.position?.y ?? 0,
            l.position?.z ?? 10
          );
          light.name = l.name;
          light.userData = merge(light, {
            objectType: ObjectTypes.SceneLight,
            name: `${ObjectTypes.SceneLight}${NAME_SEPARATOR}${light.id}`,
          });
          appThree.scene.add(light);
        }
      });
    }
    if (data.state.ui.grid.isVisible) {
      const size = 100000;
      const grid = new THREE.GridHelper(1, size / 10, "#e9e9e9", "#eee");
      grid.position.x = 0;
      grid.position.y = 0;
      grid.position.z = -500;
      grid.scale.x = size;
      grid.scale.y = size;
      grid.scale.z = size;

      grid.rotation.x = THREE.MathUtils.DEG2RAD * 90;
      grid.rotation.y = THREE.MathUtils.DEG2RAD * 0;
      grid.rotation.z = THREE.MathUtils.DEG2RAD * 0;
      grid.userData = merge(data.state.ui.grid, {
        objectType: ObjectTypes.SceneBackgroundGrid,
        name: `${ObjectTypes.SceneBackgroundGrid}${NAME_SEPARATOR}${data.state.ui.grid.id}`,
      });

      appThree.scene.add(grid);
    }
  });
};
